export interface ClientMatrixSdk {
    plugins: PluginManager;
    globalMatrix: GlobalMatrix;
    matrixSession: MatrixSession;
    app: IApp;
    ml: IMatrix;
    notificationSetting: string;
    defaultNotificationConfig: INotificationConfig;
    pluginHooks: IPluginHooks;
    MR1: MR1Impl;
    matrixApplicationUI: Application;
    matrixsdk: MatrixSDK;
    ConfigPage: typeof ConfigPage; // typeof because we're referencing an instance
    PluginCore: typeof PluginCore; // typeof because we're referencing an instance
    PrintProcessor: typeof PrintProcessor; // typeof because we're referencing an instance
    ControlCore: typeof ControlCore; // typeof because we're referencing an instance
}

declare module window {
    const matrixSdk: ClientMatrixSdk;
}

function isOutsideMatrixApp(): boolean {
    return !(window && window.matrixSdk);
}

export function getSdkInstance(): ClientMatrixSdk {
    if (isOutsideMatrixApp()) {
        throw new Error("Client SDK can only be used within Matrix Requirements application");
    }

    return window.matrixSdk;
}

export function registerPlugin(plugin: PluginCore): void {
    getSdkInstance().plugins.register(plugin);
}
